*{
    margin: 0;
    box-sizing: border-box;
}

body{
    background-color: #000000;
    height: 100vh;
    width: 100%;
}

nav{
    color: white;
    font-family: "Inter",sans-serif;
    display: flex;
    align-items: center;
    margin: 40px 240px;
}

.icons{
    width: 190px;
    display: flex;
    justify-content: space-between;
}
.social-icons:hover{
    cursor: pointer;
}

 a,.logo{
    margin-right: auto;
    width:110px;
}

.logo:hover{
    cursor: pointer;
}

.main-info{
    display: grid;
    grid-template-columns: 459px 484px;
    max-width:999px;
    align-items: center;
    justify-content: center;
    margin: 100px 210px;
}

.name-info{
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 200;
    font-size: 72px;
    line-height: 91px;
    color: #C3FCF1;
    grid-row: 1;
}

.role-info{
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 91px;
    color: #FFF300;
}
.img-info{
    grid-row: 1 / span 3;
    margin-left: 100px;
    width: 400px; 
    margin-top: -115px;
}

.about-info{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;
    width: 384px;
    margin-top: 25px;
}

.cta-btn{
    background: #C3FCF1;
    width:264px;
    height: 86px;
    border-radius: 30px;  
    font-family: 'Monda';
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 46px;
    letter-spacing: 0.03em;
    color: #000000;
    margin: -33px 328px ;
}

.cta-btn:hover{
    box-shadow: 0px 2px 2px rgb(239, 237, 233);
    cursor: pointer;
}

.scroll,.scroll-arrow{
    color: #ffffff;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
}

.scroll{
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.5em;
    text-align: center;
    margin-top: 140px;
    opacity: 1;
}

.hiddenClass{
    visibility: hidden;
}

.scroll-arrow{
    font-size: 32px;
    line-height: 39px;
    letter-spacing: 0.1em;
    transform: rotate(180deg);
    text-align: center;
}


/* Skills Sections*/

.skill-info{
    color:white;
}

.heading-skill{
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    margin: 100px 0;
}

.skill-1{
    display: flex;
    justify-content:center;
    align-items: center;
    gap: 150px;
    width: 75%;
    margin:0 220px 150px 220px;
}


.f-head{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 25px;
}

.f-para{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 30px;
    opacity: 0.6;
}

.skill-2{
    display: flex;
    justify-content:center;
    align-items: center;
    gap: 150px;
    width: 75%;
    margin:0 220px 150px 220px;
}

.skill-2-info{
    width: 60%;
}

.skill-3{
    display: flex;
    justify-content:center;
    align-items: center;
    gap: 200px;
    width: 75%;
    margin:0 220px 150px 220px;
}

/* Footer */

.footer{
    background-color: #a3f9e8;
    width: 100%;
    color: #000000;
    font-family: "Outfit",sans-serif;
    font-size: 10px;
    height: 45px;
}

.footer > div{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}


