@media screen and (min-width:340px) and (max-width:600px){
    nav{
        margin: auto;
        padding: 15px;
        width: 100%;
    }
    .logo{
        width: 65%;
    }

    .social-icons{
        width: 40%;
    }

    .icons{
        width: 40%;
    }

    

    .main-info{
        width: 60%;
        margin: 40px 60px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .img-info{
        margin: 0;
        width: 60%;
    }
    .name-info{
        font-weight: 200;
        font-size: 20px;
        line-height: 45px;
        color: #C3FCF1;
        grid-row: 1;
    }
    .role-info{
        font-size: 20px;
        text-align: left;
    }

    .about-info{
        margin: 5px 0;
        font-size: 12px;
        line-height: 16px;
        width: 100%;
    }
    .btn{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .cta-btn{
        width:35%;
        height:50px;
        margin:0;
        font-size: 12px;
    }

    .scroll{
        font-size: 10px;
        margin-top: 20px;
    }

    .scroll-arrow{
        font-size: 20px;
    }

    .heading-skill{
        font-size: 22px;
        line-height: 28px;
        margin: 80px auto;
    }

    .skill-1,.skill-2,.skill-3{
        width: 65%;
        margin: 40px auto;
        gap:50px;
        flex-direction: column;
        gap: 30px;
    }

    .skill-1{
        flex-direction: column-reverse;
    }

    .skill-3{
        flex-direction: column-reverse;
    }

    .f-head{
        font-size: 16px;
        line-height: 19px;
    }

    .f-para{
        font-size: 12px;
        line-height: 14px;
        width: 100%;
    }

    .f-img,.sql-img{
        width: 80%;
        height:125px;
    }

    .python-img{
        width:50%;
        height: 100px;
    }

    .footer{
        height: 100%;
        font-size: 50%;
       
    }

}


